import React from "react";

const New = ({ cardPublish }) => {
  const [time, setTime] = React.useState(0);

  React.useEffect(() => {
      getServerTime();
  }, [])

  const getServerTime = () => {
    const url = "http://worldtimeapi.org/api/timezone/Europe/Moscow";
    fetch(url)
      .then((response) => {
        return response.json();
      })
      .then((date) => {
        const unixtime = date.unixtime;
        let distance = unixtime * 1000 - cardPublish;

        // get days
        let result = Math.floor(distance / (1000 * 60 * 60 * 24));

        setTime(result);
      });
  };

  return <div className="card-new">NEW {time}</div>
};

export default New;
